import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import gaugeGraph from '../../../assets/img/gauge-graph.svg'
import DeriNumberFormat from '../../../utils/DeriNumberFormat'
import Button from '../../../components/Button/Button'
import { SECONDARY, DEPOSIT, WITHDRAW } from '../../../utils/Constants'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'
import { show, hide } from 'react-functional-modal'
import MarginPanel from '../../../components/MarginPanel/MarginPanel'
import { observer, inject} from 'mobx-react';
import './accountInfo.less'
import {useAlert} from 'react-alert'
import Gauge from '../../../components/Gauge/Gauge';
import TradeWarnning from '../SideBar/TradeWarnning'
import classNames from 'classnames'
import Icon from '../../../components/Icon/Icon'

function DynEffectiveBalanceDisplay({trading,lang}){
  const [isShowCollect, setIsShowCollect] = useState(false)
  const showClass = classNames('xvs-earned',{
    show : isShowCollect
  })
  return trading.symbolInfo.isAllV3 
  ? <div className='value v3'>
      <div className='dyn-eff-bal'>
        <DeriNumberFormat value={trading.accountInfo.dynEffMargin} decimalScale='2' suffix={` ${trading.tradeBToken}`}/> 
        <div><Icon token={isShowCollect ? 'arrow-up' : 'arrow-down'} alt='collect' width='24' height='24' onClick={() => setIsShowCollect(!isShowCollect)}/></div>
      </div>
      <div className={showClass}>
        {lang['xvs-earned']} 
        <DeriNumberFormat decimalScale='2' value={trading.accountInfo.xvsEarned} width='16px'/> 
        <Button bgColor='rgba(55, 86, 205, 0.5)' className='collect' fontSize='12' fontWeight='400' width='40' height='16' label={lang['collect']}/></div>
    </div>
  : <div className='value'><DeriNumberFormat value={trading.accountInfo.dynEffMargin} decimalScale='2' suffix={` ${trading.tradeBToken}`}/> </div>
}

function AccountInfo({trading,wallet,lang}){
  const alert = useAlert()
  const [isShowWarn, setIsShowWarn] = useState(false)
  const [warnContent, setWarnContent] = useState('')
  const showWarnClass = classNames({show : isShowWarn})
  const showMarginPanel = (type) => {
    show(<MarginPanel type={type} trading={trading} wallet={wallet} lang={lang} pool={trading.pool} alert={alert}/> ,{
      key  : 'marginPanel',
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)" ,
        zIndex : 2,
      }
    })
  }
  useEffect(() => {
    if(trading.accountInfo){
      if(trading.accountInfo.marginUsagePercent >=100 ){
        setWarnContent(lang['over-margin-100%-warn-tip'])
        setIsShowWarn(true)
      }
      if(trading.accountInfo.marginUsagePercent >= 180) {
        setWarnContent(lang['over-margin-180%-warn-tip'])
        setIsShowWarn(true)
      }
      if(trading.accountInfo.marginUsagePercent < 100){
        setIsShowWarn(false)
      }
    }
  }, [trading.accountInfo])

  return(
    <div className='account' >
      <div className='a-info'>
          <div className='content'>
            <div className='detail-graph'>
              <div className='detail'>
                <div className='title row'>{lang['account-info']}</div>
                <div className='row'>
                  <div><UnderlineText text={lang['dynamic-effective-balance']} multiline tip={lang['dynamic-effective-balance-tip']}/></div>
                  <DynEffectiveBalanceDisplay trading={trading} lang={lang}/>
                </div>
                <div className='row'>
                  <div><UnderlineText text={lang['margin-usage']} tip={lang['margin-usage-tip']}/></div>
                  <div className='value'><DeriNumberFormat value={trading.accountInfo.marginUsage} suffix={` ${trading.tradeBToken}`} decimalScale='2' /></div>
                </div>
                <div className='row'>
                  <div><UnderlineText text={lang['available-margin']} tip={lang['available-margin-tip']} multiline/></div>
                  <div className='value'><DeriNumberFormat value={trading.accountInfo.availableMargin} decimalScale='2' suffix={` ${trading.tradeBToken}`}/></div>
                </div>
              </div>
              <div className='graph'>
                <Gauge accountInfo={trading.accountInfo} lang={lang}/>
              </div>
          </div>
          <TradeWarnning content={warnContent} className={showWarnClass}/>
          <div className='info-footer'>
            <Button lang={lang} trading={trading} type={SECONDARY} icon='deposit' isReady={trading.isAccountReady} outline label={lang['deposit'].toUpperCase()} onClick={() => showMarginPanel(DEPOSIT)}/> 
            <Button lang={lang} trading={trading} outline type={SECONDARY} icon='withdraw' isReady={trading.isAccountReady} label={lang['withdraw'].toUpperCase()} onClick={() => showMarginPanel(WITHDRAW)}/>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet','trading')(observer(AccountInfo))